import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../FinanceManager/Income/Income.module.css";
import { useOneApplicationStaffActionShow } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { Fragment } from "react";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
const WhichActionStaff = ({ onClose, studentId }) => {
  const { t } = useTranslation();
  const {
    oneApplicationStaffActionShow,
    oneApplicationStaffActionShowLoading,
  } = useOneApplicationStaffActionShow({
    data: {
      sid: studentId,
    },
    skip: !studentId,
  });
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("student_admission_logs")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.card_container_inner}>
            {oneApplicationStaffActionShow?.map((st, index) => (
              <Fragment key={`${st?.key}${index}`}>
                <h6>
                  <span
                    style={{
                      fontWeight: "400",
                    }}
                  >
                    {st?.key}
                    {" : "}
                  </span>

                  {st?.value ?? ""}
                </h6>
                {st?.latest ? (
                  <p>
                    {t("date_and_time")}
                    {": "}
                    {dayjs(st?.latest)?.format("DD/MM/YYYY")}
                    {", "}
                    {localTimeFormat(st?.latest, "LT")}
                  </p>
                ) : null}
                <BorderBottom />
              </Fragment>
            ))}
          </div>
          {oneApplicationStaffActionShowLoading && <Loading />}
          {oneApplicationStaffActionShow?.length === 0 && (
            <EmptyMessage
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
              title="No logs."
            />
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default WhichActionStaff;
