import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import styleIncome from "../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import AdmissionApplicationCalender from "../../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/AdmissionApplicationRequest/OngoingApplication/AdmissionApplicationCalender";
import style from "./OneOngoingAdmission.module.css";
import {
  useAdmissionCompleteApplication,
  useAdmissionOngoingApplicationDailyCollect,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
const OneOngoingAdmissionDetail = ({
  applicationDetail,
  onClose,
  applicationDetailApplicablePending,
  isDepartment,
}) => {
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const getTodayDate = new Date();
  const [disabled, setDisabled] = useState(false);
  const [isViewAll, setIsViewAll] = useState(false);
  const [validDate, setValidDate] = useState(
    getTodayDate.toISOString()?.substring(0, 10)
  );
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const [admissionCompleteApplication] = useAdmissionCompleteApplication();
  const { admissionOngoingApplicationDailyCollect } =
    useAdmissionOngoingApplicationDailyCollect({
      data: {
        appId: applicationDetail?._id,
        valid_date: validDate,
      },
      skip: !applicationDetail?._id,
    });

  const onCompleteApplication = () => {
    if (applicationDetail?._id) {
      setDisabled((pre) => !pre);
      admissionCompleteApplication({
        appId: applicationDetail?._id,
      })
        .then((res) => {
          if (res?.data?.complete_status) {
            let url =
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/admission?type=ongoing-detail`
                : `/q/${params.username}/member/staff/admission?type=ongoing-detail`;
            navigate(url, {
              state: getQuery.state,
            });
          } else
            setSnackbar({
              msg: res?.data.message,
              open: true,
              variant: "error",
            });
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info("Safs", getQuery.state);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={styleIncome.modal_container}>
          <UserMemberContentWrapper>
            <div className={styleIncome.modal_container_outer_header}>
              <div className={styleIncome.modal_container_header}>
                <h6
                  style={{
                    cursor: "pointer",
                    color: "#436dfa",
                  }}
                  onClick={() => setIsViewAll((pre) => !pre)}
                >
                  {applicationDetail?.applicationName}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <AdmissionApplicationCalender setValidDate={setValidDate} />
            {!isViewAll ? (
              <>
                <div className={style.content2}>
                  <div className={style.appinforow}>
                    <h6>{t("total_received_application")}</h6>
                    <h6>
                      {admissionOngoingApplicationDailyCollect?.request_count ??
                        0}
                    </h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_selected_application")}</h6>
                    <h6>
                      {admissionOngoingApplicationDailyCollect?.select_count ??
                        0}
                    </h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_confirmed_application")}</h6>
                    <h6>
                      {admissionOngoingApplicationDailyCollect?.confirm_count ??
                        0}
                    </h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_alloted_application")}</h6>
                    <h6>
                      {admissionOngoingApplicationDailyCollect?.allot_count ??
                        0}
                    </h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_cancelled_application")}</h6>
                    <h6>
                      {admissionOngoingApplicationDailyCollect?.cancel_count ??
                        0}
                    </h6>
                  </div>
                </div>
                <div className={style.content3}>
                  <div className={style.appinforow1}>
                    <h6>{t("total_fees_received")}</h6>
                    <h6>
                      Rs. {admissionOngoingApplicationDailyCollect?.paid ?? 0}
                    </h6>
                  </div>

                  <div
                    className={`${style.appinforow2} ${style.appinforowred}`}
                  >
                    <h6>{t("total_fees_pending")}</h6>
                    <h6>
                      Rs. {admissionOngoingApplicationDailyCollect?.remain ?? 0}
                    </h6>
                  </div>
                  <div
                    className={`${style.appinforow2} ${style.appinforowred}`}
                  >
                    <h6>{t("total_applicable_fees_pending")}</h6>
                    <h6>
                      Rs.{" "}
                      {admissionOngoingApplicationDailyCollect?.applicable_pending ??
                        0}
                    </h6>
                  </div>

                  {isAuthorizedComplete[
                    getQuery.state?.isAuthorization
                  ] ? null : (
                    <>
                      {/* {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : ( */}
                      {getQuery.state?.role || isDepartment ? null : (
                        <CreateButton
                          label={t("complete_application")}
                          onAction={onCompleteApplication}
                        />
                      )}
                    </>
                  )}
                </div>
              </>
            ) : (
              <>
                <div className={style.content2}>
                  <div className={style.appinforow}>
                    <h6>{t("total_received_application")}</h6>
                    <h6>{applicationDetail?.receievedCount}</h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_selected_application")}</h6>
                    <h6>{applicationDetail?.selectCount}</h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_confirmed_application")}</h6>
                    <h6>{applicationDetail?.confirmCount}</h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_alloted_application")}</h6>
                    <h6>{applicationDetail?.allotCount}</h6>
                  </div>
                  <div className={style.appinforow}>
                    <h6>{t("total_cancelled_application")}</h6>
                    <h6>{applicationDetail?.cancelCount}</h6>
                  </div>
                </div>
                <div className={style.content3}>
                  <div className={style.appinforow1}>
                    <h6>{t("total_fees_received")}</h6>
                    <h6>Rs. {applicationDetail?.collectedFeeCount}</h6>
                  </div>

                  <div
                    className={`${style.appinforow2} ${style.appinforowred}`}
                  >
                    <h6>{t("total_fees_pending")}</h6>
                    <h6>Rs. {applicationDetail?.remainingFee}</h6>
                  </div>
                  <div
                    className={`${style.appinforow2} ${style.appinforowred}`}
                  >
                    <h6>{t("total_applicable_fees_pending")}</h6>
                    <h6>Rs. {applicationDetailApplicablePending ?? 0}</h6>
                  </div>

                  {isAuthorizedComplete[
                    getQuery.state?.isAuthorization
                  ] ? null : (
                    <>
                      {/* {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : ( */}
                      {getQuery.state?.role || isDepartment ? null : (
                        <CreateButton
                          label={t("complete_application")}
                          onAction={onCompleteApplication}
                        />
                      )}
                    </>
                  )}
                </div>
              </>
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default OneOngoingAdmissionDetail;
const isAuthorizedComplete = {
  isCompleted: true,
};
