import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../PlateformUser/PlateformUser.module.css";
// import { useTranslation } from "react-i18next";
const ApplicationSubjectListCard = ({
  sub,
  appId,
  isDepartment,
  parentUrl,
}) => {
  // const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/admission?type=selected-subject`
          : isDepartment
          ? parentUrl
          : `/q/${params.username}/member/staff/admission?type=selected-subject`
      }
      state={{
        ...getQuery.state,
        smid: sub?._id,
        appId: appId,
        subjectName: sub?.subjectName,
      }}
      className={style.assign_show_list_each}
      style={{
        border: "0.5px solid #cacaca",
        marginBlock: "0.4rem",
        borderRadius: "6px",
        padding: "0.8rem 1rem",
      }}
    >
      <div className={style.assign_each_paragraph}>
        <h6>{sub?.subjectName ?? ""}</h6>
        {/* <p>
          {t("student")}
          {": "} {sub?.studentCount ?? 0}
        </p> */}
      </div>
    </Link>
  );
};

export default ApplicationSubjectListCard;
