import { useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import { useOneApplicationSubjectCountList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../PlateformUser/PlateformUser.module.css";
import ApplicationSubjectListCard from "./ApplicationSubjectListCard";

const ApplicationSubjectList = ({
  appId,
  isDepartment,
  onClose,
  parentUrl,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const {
    oneApplicationSubjectCountList,
    oneApplicationSubjectCountListLoading,
  } = useOneApplicationSubjectCountList({
    data: {
      aid: appId,
      search: search,
    },
    skip: !appId,
  });

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !oneApplicationSubjectCountListLoading &&
      oneApplicationSubjectCountList?.length >= 0
    ) {
      if (e.target.value) {
        setSearchCopy(e.target.value);
      } else {
        setSearchCopy("");
        setSearch("");
      }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setSearch(searchCopy);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("subject_list")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {oneApplicationSubjectCountList?.map((sub) => (
            <ApplicationSubjectListCard
              sub={sub}
              key={sub?._id}
              appId={appId}
              isDepartment={isDepartment}
              parentUrl={parentUrl}
            />
          ))}
          {oneApplicationSubjectCountListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <QvipleLoader />
            )
          ) : search ? (
            oneApplicationSubjectCountList?.length === 0 && (
              <EmptyMessage
                title="No subject found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            oneApplicationSubjectCountList?.length === 0 && (
              <EmptyMessage
                title="No subject."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default ApplicationSubjectList;
